            <!-- manage -->
            <div class="c-header-panel c-header-manage" id="c-header-manage">
                <span class="u-post u-manage">
                    <svg class="icon" viewBox="0 0 1024 1024" xmlns="http://www.w3.org/2000/svg" width="18" height="18" v-bind:class="'u-add'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M224 423.84V231.744l192-.096.096 192.096L224 423.84zm192.096-256.096H223.904A64 64 0 00160 231.68v192.192a64 64 0 0063.904 63.904h192.192A64 64 0 00480 423.84V231.68a64 64 0 00-63.904-63.904zM224 807.84V615.744l192-.096.096 192.096L224 807.84zm192.096-256.096H223.904A64 64 0 00160 615.68v192.192a64 64 0 0063.904 63.904h192.192A64 64 0 00480 807.84V615.68a64 64 0 00-63.904-63.904zm287.968-88.128l-135.84-135.84 135.712-135.84L839.84 327.68 704.064 463.616zm181.024-181.024L749.184 146.688a64 64 0 00-90.368 0L522.912 282.56a63.456 63.456 0 00-18.656 45.056 63.456 63.456 0 0018.656 45.312l135.904 135.936a63.904 63.904 0 0090.368-.032l135.904-135.904a63.936 63.936 0 000-90.368zM608 807.84V615.744l192-.096.096 192.096L608 807.84zm192.096-256.096H607.904A64 64 0 00544 615.68v192.192a64 64 0 0063.904 63.904h192.192A64 64 0 00864 807.84V615.68a64 64 0 00-63.904-63.904z" fill="#fff"/></svg>
                </span>
                <ul class="u-menu u-pop-content">
                    <li v-for="item in finalPanel" :key="item.label">
                        <a :href="item.link" target="_blank"
                            ><i :class="item.icon || 'el-icon-present'"></i> {{ item.label }}</a
