    <div class="w-boxcoin-user" v-if="allowBoxcoin">
        <el-tooltip effect="dark" content="投币" placement="top-start">
            <div class="w-boxcoin-block" @click="openBoxcoinPop">
                <svg data-name="图层 1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 733.26 674.32" v-bind:class="'u-icon'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M733.22 80.56v382.25a55.77 55.77 0 00-2.66 7.13c-9.46 41.47-43.31 71.45-85.75 73.13-38.33 1.51-76.75 1-115.12.66-14.61-.13-26 4.45-36.23 14.89-34.07 34.7-68.64 68.91-103.08 103.26a90.49 90.49 0 01-7.07 5.8 91.16 91.16 0 01-7.13-5.81q-61.47-61.41-122.87-122.93c-12.62-12.69-27.51-19-45.44-18.92-36.72.18-73.44-.17-110.16.11-43.42.32-73.78-33.64-73.5-73.36.91-132.57.37-265.15.22-397.73 0-4.79-1.83-9.58-2.8-14.36C41.51 11.37 66.63-.03 97.41 0q265.06.27 530.12.16a176.82 176.82 0 0125.51 1.71c37.43 5.5 61.83 27.08 75 62 2.07 5.44 3.5 11.12 5.18 16.69zm-342.1 365.18c12.11-8 24.57-15.47 36.27-24 37.72-27.56 72.32-58.37 98.8-97.38 16.43-24.21 27.35-50.52 29.36-79.89 1-15.17-1.6-30-6.88-44.44-18.29-50-68.37-75.28-117-68.71-21.39 2.89-39.77 12.23-56 26.29-7 6-10.88 6.15-17.55.2-23.76-21.19-51.75-29.45-83-27-14.19 1.1-28.05 4.41-40.14 12.71-8.16 5.65-17.39 10.22-24.29 17.13-33.21 33.24-40.75 72.83-26.59 117 11.19 34.93 32.83 63.32 57.88 89.2 33 34.12 71 62 111.58 86.54 6.72 4.07 14 5.53 21.17 2.31 5.81-2.63 10.96-6.6 16.39-9.96z" fill="#fe7e91"/><path d="M383.35 667.64c-13 9.8-25.55 8.81-37.78-3.35q-45.62-45.44-91.08-91c-5.9-5.9-11.82-11.78-17.7-17.7a38.13 38.13 0 00-28-11.71c-40.52 0-81.05.84-121.54-.08-19.15-.43-36.5-8.46-51.52-20.63C16.3 507.41 5.16 486.64.48 462.39c-.93-4.84-.21-10-.21-15 0-119.95-.23-239.9.21-359.85.08-19.84 8.47-37.49 21.15-52.86 1 4.78 2.79 9.57 2.8 14.36.15 132.6.69 265.16-.21 397.73-.28 39.72 30.08 73.68 73.5 73.36 36.72-.28 73.44.07 110.16-.11 17.93-.09 32.82 6.23 45.44 18.92q61.3 61.62 122.9 122.93a91.16 91.16 0 007.13 5.77z" fill="#fe5f7a"/><path d="M235.01 143.49c-2.19 3.31-4.12 6.84-6.62 9.91-28.13 34.58-31.36 72.91-16.65 113.59 10.86 30 29.47 55.26 51.15 78.36 36 38.33 77.9 69 122.71 96.08a55.38 55.38 0 015.52 4.31c-5.43 3.36-10.58 7.33-16.36 9.92-7.18 3.22-14.45 1.76-21.17-2.31-40.53-24.55-78.55-52.42-111.58-86.54-25.05-25.88-46.69-54.27-57.88-89.2-14.16-44.16-6.62-83.75 26.59-117 6.9-6.9 16.13-11.47 24.29-17.12z" fill="#fedadd"/></svg>
                <span class="u-count" v-if="boxcoin">{{boxcoin}}</span>
            </div>
        </el-tooltip>
        <el-dialog title="投币打赏" :visible.sync="visible" custom-class="w-boxcoin-pop" append-to-body :close-on-click-modal="false">
            <div class="w-boxcoin-user-content">
                <div class="u-left">
                    <em class="u-label">当前拥有盒币</em>
                    <b>{{left}}</b>
                    <a class="u-charge" :href="chargeLink" target="_blank">[充值]</a>
                </div>
                <div class="u-list">
                    <em class="u-label">❤️ 打赏</em>
                    <Contributors v-if="authors && authors.length" :authors="authors" @chosen="handleChosen" />
                    <div class="u-points">
                        <el-radio-group v-model="count">
                            <el-radio :label="item" v-for="item in fitPoints" :key="item" border>
                                <b>{{item}}</b>盒币
                            </el-radio>
                        </el-radio-group>
                    </div>
                </div>
                <div class="u-msg">
                    <em class="u-label">📝 寄语</em>
                    <div class="u-input">
                        <el-input
                            v-model="remark"
                            placeholder="请输入寄语（必填）"
                            :minlength="2"
                            :maxlength="30"
                            show-word-limit
                        ></el-input>
                    </div>
                </div>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="visible = false">取 消</el-button>
                <el-button type="primary" @click="submit" :disabled="!ready">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import { rewardBoxcoin } from "../../service/thx.js";
import User from "@jx3box/jx3box-common/js/user";
import Contributors from './Contributors.vue';
export default {
    name: "BoxcoinUser",
    props: ["boxcoin", "postType", "postId", "userId", "own", "points", "authors",'client'],
    components: {
        Contributors
    },
    data: function () {
        return {
            visible: false,

            count: 0,
            remark: "辛苦了，谢谢大大！",

            left : this.own,

            chargeLink: "/vip/boxcoin?redirect=" + location.href,

            chosen: '', // 被选中的人
        };
    },
    computed: {
        ready: function () {
            return this.isNotSelf && this.isEnough && this.count && this.remark;
        },
        isNotSelf: function () {
            return this.userId != User.getInfo().uid;
        },
        isEnough: function () {
            return this.left && this.left >= this.count;
        },
        allowBoxcoin : function (){
            return this.postType && this.postId && (this.userId || (this.authors && this.authors.length))
        },
        hostClient : function (){
            return location.href.includes('origin') ? 'origin' : 'std'
        },
        fitPoints : function (){
            return this.points//.filter(item => item <= this.left)
        }
    },
    watch: {
        own : function (val){
            this.left = val
        }
    },
    methods: {
        openBoxcoinPop: function () {
            if (User.isLogin()) {
                this.visible = true;
            } else {
                User.toLogin();
            }
        },
        // 选择要打赏的对象
        handleChosen(userId) {
            this.chosen = userId
        },
        submit: function () {
            rewardBoxcoin(this.postType, this.postId, this.chosen || this.userId, this.count, {
                remark: this.remark,
                client : this.client || this.hostClient
            })
                .then((res) => {
                    this.$message({
                        message: "操作成功",
                        type: "success",
                    });
                    return res.data.data
                })
                .then((data) => {
                    // 1.扣除额度
                    this.left -= this.count;
                    // 2. 将新增emit出去
                    this.$emit('updateRecord', data);
                })
                .finally(() => {
                    this.visible = false;
                });

        },
        init: function () {},
    },
    created: function () {},
    mounted: function () {},
};
</script>

