<template>
  <aside
    class="c-sidebar-left c-sidebar m-theme"
    :class="{
            isclose: !isOpen,
            isopen: isOpen,
            'without-bread': withoutBread,
        }"
    v-if="!isApp"
    :style="{backgroundImage:bg}"
  >
    <div class="c-sidebar-left-inner">
      <slot></slot>
    </div>
    <span
      class="c-sidebar-left-toggle"
      :class="!isOpen && 'close-sidebar-left'"
      @click="toggleLeftSide"
      :title="isOpen ? '收起侧边栏' : '打开侧边栏'"
    >
      <!-- <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" v-show="isOpen" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M452.033.142H60.251C27.185.142.284 27.043.284 60.109v79.956h39.978V60.109c0-11.022 8.967-19.989 19.989-19.989h391.783c11.022 0 19.989 8.967 19.989 19.989v391.783c0 11.022-8.967 19.989-19.989 19.989H60.251c-11.022 0-19.989-8.967-19.989-19.989v-79.956H.284v79.956c0 33.066 26.901 59.967 59.967 59.967h391.783c33.066 0 59.967-26.901 59.967-59.967V60.109C512 27.043 485.099.142 452.033.142z"/><path d="M364.082 236.011H76.526l75.957-75.958-28.268-28.268L0 256l124.215 124.215 28.268-28.268-75.957-75.958h287.556z"/></svg> -->
      <span v-show="isOpen"><i class="el-icon-arrow-left"></i></span>
      <span v-show="!isOpen"><i class="el-icon-arrow-right"></i></span>
      <!-- <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" v-show="!isOpen" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M255.15 468.625H63.787c-11.737 0-21.262-9.526-21.262-21.262V64.638c0-11.737 9.526-21.262 21.262-21.262H255.15c11.758 0 21.262-9.504 21.262-21.262S266.908.85 255.15.85H63.787C28.619.85 0 29.47 0 64.638v382.724c0 35.168 28.619 63.787 63.787 63.787H255.15c11.758 0 21.262-9.504 21.262-21.262 0-11.758-9.504-21.262-21.262-21.262z"/><path d="M505.664 240.861L376.388 113.286c-8.335-8.25-21.815-8.143-30.065.213s-8.165 21.815.213 30.065l92.385 91.173H191.362c-11.758 0-21.262 9.504-21.262 21.262 0 11.758 9.504 21.263 21.262 21.263h247.559l-92.385 91.173c-8.377 8.25-8.441 21.709-.213 30.065a21.255 21.255 0 0015.139 6.336c5.401 0 10.801-2.041 14.926-6.124l129.276-127.575A21.303 21.303 0 00512 255.998c0-5.696-2.275-11.118-6.336-15.137z"/></svg> -->
    </span>
  </aside>
</template>

<script>
import Bus from "../service/bus";
import { isApp } from "../assets/js/app.js";
import {getDecoration} from "../service/cms"
import { __imgPath } from "@jx3box/jx3box-common/data/jx3box.json";
import User from '@jx3box/jx3box-common/js/user'
export default {
  name: "LeftSidebar",
  props: ["open", "withoutBread","uid"],
  data: function () {
    return {
      isOpen: true,
      user_id:null,
      isApp: isApp(),
      bg:""
    };
  },
  computed: {
    stickyHeader: function () {
      return this.withoutBread;
    }
  },
  watch: {
    open: function (newval) {
      this.isOpen = newval === undefined ? true : !!newval;
    },
    uid: function (newval) {
      this.user_id = newval ;
      this.getDecoration()

    },
  },
  methods: {
    toggleLeftSide: function () {
      let status = !this.isOpen;
      Bus.$emit("toggleLeftSide", status);
    },
    showDecoration:function(val,type){
        return __imgPath + `decoration/images/${val}/${type}.png`;
    },
    getDecoration(){
      if(!this.user_id){
        return;
      }
      let decoration_sidebar=sessionStorage.getItem('decoration_sidebar'+this.user_id)
      if(decoration_sidebar == 'no'){
        this.bg =""
        return;
      }
      //已有缓存，读取解析
      if(decoration_sidebar){
        this.setDecoration(JSON.parse(decoration_sidebar))
        return;
      }
      getDecoration({using:1,user_id:this.user_id,type:'sidebar'}).then(data=>{
        let res=data.data.data
        if(res.length==0){
          //空 则为无主题，不再加载接口，界面设No
          sessionStorage.setItem('decoration_sidebar'+this.user_id,'no')
          this.bg =""
          return;
        }
        sessionStorage.setItem('decoration_sidebar'+this.user_id,JSON.stringify(res[0]))
        this.setDecoration(res[0])
        // let decoration=res.filter(val => {
        //   return val.type === 'sidebar'
        // })
        // if(decoration.length>0){
         
        //   this.setDecoration(decoration[0])
        // }else{
        //   //空 则为无主题，不再加载接口，界面设No
        //   sessionStorage.setItem('decoration_sidebar'+this.user_id,'no')
        //   this.bg =""
        // }
      })
    },
    setDecoration(decoration_sidebar){
        this.bg = `url(${this.showDecoration(decoration_sidebar.val,decoration_sidebar.type)})`;
    }
  },
  mounted: function () {
    Bus.$on("toggleLeftSide", (data) => {
      this.isOpen = data;
    });

    if (window.innerWidth < 1024) {
      this.isOpen = false;

      document.addEventListener("click", function () {
        Bus.$emit("toggleLeftSide", false);
      });
    }
  },
  created: function () {
    this.isOpen = this.open === undefined ? true : this.open;
    this.user_id = this.uid
    this.getDecoration()
  },
};
</script>

<style lang="less">
@import "../assets/css/left-sidebar.less";
